:root {
    /* Colors */
    --hue-color: 0;
    --white-color: #fff;
    /* z index */
    --z-normal: 1;
    --z-tooltip: 10;
  }

.clock-circle {
  position: relative;
  width: 100%;
  aspect-ratio: 1/1;
  box-shadow: -6px -6px 16px var(--white-color),
    6px 6px 16px hsla(var(--hue-color), 30%, 86%, 1),
    inset 6px 6px 16px hsla(var(--hue-color), 30%, 86%, 1),
    inset -6px -6px 16px var(--white-color);
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.05);
  backdrop-filter:  blur(20px);
  border: 4px solid transparent;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.4s;
}

.clock-content {
  align-self: center;
  row-gap: 2rem;
}
.clock-twelve,
.clock-three,
.clock-six,
.clock-nine {
  position: absolute;
  width: 10%;
  height: 1px;
  background-color: hsl(var(--hue-color), 53%, 49%);
}
.clock-twelve,
.clock-six {
  transform: translateX(-50%) rotate(90deg);
}
.clock-twelve {
  top: 1.25rem;
  left: 50%;
}
.clock-three {
  top: 50%;
  right: 0.75rem;
}
.clock-six {
  bottom: 1.25rem;
  left: 50%;
}
.clock-nine {
  left: 0.75rem;
  top: 50%;
}
.clock-nine {
  left: 0.75rem;
  top: 50%;
}
.clock-rounder {
  width: 0.75rem;
  height: 0.75rem;
  background-color: hsl(var(--hue-color), 53%, 49%);
  border-radius: 50%;
  border: 2px solid hsl(var(--hue-color), 24%, 94%);
  z-index: var(--z-tooltip);
}
.clock-hour,
.clock-minutes,
.clock-seconds {
  position: absolute;
  display: flex;
  justify-content: center;
}

.clock-hour {
  width: 1rem;
  height: 70%;
}
.clock-hour::before {
  content: '';
  position: absolute;
  background-color: hsl(var(--hue-color), 12%, 35%);
  width: 0.25rem;
  height: calc(55% + 0.1rem);
  border-radius: 0.75rem;
  z-index: var(--z-normal);
}
.clock-minutes {
  width: 1rem;
  height: 82%;
}
.clock-minutes::before {
  content: '';
  position: absolute;
  background-color: hsl(var(--hue-color), 12%, 35%);
  width: 0.25rem;
  height: calc(55% + 0.2rem);
  border-radius: 0.75rem;
  z-index: var(--z-normal);
}
.clock-seconds {
  width: 1rem;
  height: 92%;
}
.clock-seconds::before {
  content: '';
  position: absolute;
  background-color: hsl(var(--hue-color), 53%, 49%);
  width: 0.125rem;
  height: calc(55% + 0.5rem);
  border-radius: 0.75rem;
  z-index: var(--z-normal);
}
